@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');


*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

body{
    background-color: #00081d;
    color: white;
    position: relative;
    width: 100%;
    min-height: 100vh;
}

/* wrapper */

.wrapper{
    max-width: 1440px;
    margin: auto;
}

.movie-item{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    width: 700px;
    margin: auto;
    padding: 30px 20px;
    background-color: #6A8DE6;
    border-radius: 25px;
    box-shadow: 10px 9px 20px 0px #91a1cc;
}

.movie-name{
    text-align: center;
}

.movie-item__wrapper{
    padding-bottom: 170px;
}

.preview{
    width: 100%;
    display: flex;
    position: relative;
}

.trailer{
    position: absolute;
    text-decoration: none;
    display: flex;
    align-items: center;
    bottom: 10px;
    left: 250px;
    right: 370px;
    border: 1px solid white;
    border-radius: 50%;
    padding: 5px 11px;
    color: red;
    cursor: pointer;
    background-color: white;
    transition: left 0.5s;
    overflow: hidden;
}

.trailer:hover{
    left: 185px;
    border-radius: 30px;
    padding: 5px 15px;
    gap: 5px;
}

.trailer:hover .popup_text{
    display: block;
    opacity: 1;
    width: fit-content;
    height: fit-content;
}
.popup_text{
    overflow: hidden;
    opacity: 0;
    font-weight: bold;
    transition: all 0.5s;
}

.movie_error{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50vh;
}

.description{
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
    margin-left: 30px;
    width: 100%;
}

.poster{
    border-radius: 15px;
}

.no-poster{
    width: 300px;
    height: 437px;
}
.rate{
    display: flex;
    gap: 15px;
    align-items: center;
}

.rate-number{
    font-size: 21px;
    font-weight: bold;
}
.genres-list{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    gap: 15px;
}

.genre{
    border: 1px solid #b2b7c1;
    border-radius: 25px;
    padding: 5px 10px;
}

.info{
    display: flex;
    gap: 20px;
}

.plot{
    text-align: justify;
    align-self: flex-start;
}

.writter{
    display: flex;
    align-self: flex-start;
    padding-left: 5px;
    flex-wrap: wrap;
}

.actors-header{
    margin-right: 10px;
}

.cast{
    align-self: flex-start;
    display: flex;
    flex-wrap: wrap;
}

.country{
    display: flex;
    align-self: flex-start;
    padding-left: 5px;
}


.btn{
    position: absolute;
    bottom: 0px;
    width: stretch;
    height: 50px;
    background-color: transparent;
    border: 1px solid #b2b7c1;
    border-radius: 15px;
    color: white;
    font-size: 20px;
    cursor: pointer;
    margin-top: 10px;
    transition: all 0.3s;
}
.btn:hover{
    background-color: #00081d;
    border: 1px solid #00081d;
}


/* Search-querry */

.search-querry{
    width: 70%;
    margin: auto auto 30px auto;
    text-align: center;
}
.search-querry p{
    margin-bottom: 10px;
    font-size: 30px;
    font-weight: bold;
}

.search-querry-content{
    display: flex;
    align-items: center;
    gap: 15px;
}


.search-btn{
    background-color: transparent;
    border: 1px solid #6A8DE6;
    padding: 12px 45px;;
    border-radius: 50px;
    color: white;
    font-size: 20px;
    cursor: pointer;
    transition: all 0.3s;
}

.search-btn:hover{
    background-color: #6A8DE6;
}

/* Movie-List */


.movie-list{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    gap: 70px;
    margin: auto;
    padding: 20px 20px 100px 20px;
}

.showTopBtn-wrapper{
    display: flex;
    justify-content: center;
    margin-bottom: 40px;
}

.movie-object{
    color: white;
    display: flex;
    gap: 40px;
    background-color: #6A8DE6;
    border-radius: 25px;
    box-shadow: 10px 9px 20px 0px #91a1cc;
    padding: 30px 20px;
    transition: all 0.5s;
    width: 450px;
    cursor: pointer;
}

.movie-object:hover{
    scale: 1.2;
}

.movie-poster{
    width: 150px;
    height: 250px;
}

.movie-description{
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.movie-header{
    font-size: 1.2em;
}

.movie-info{
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.movie-countries{
    display: flex;
    gap: 7px;
    flex-wrap: wrap;
}

.movie-genres{
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
    font-size: 14px;
    font-weight: bold;
}


.movie-fact{
    display: flex;
    gap: 15px;
}


/* Layout */


.header{
    background-color: #424579;
    height: 100px;
    margin-bottom: 100px;
}

.header-wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    padding: 0px 15px;
}

.logo{
    font-size: 40px;
    font-weight: bold;
}

.logo  span{
    color: #bac7e8
}

.pagination{
    display: flex;
    gap: 15px;
}

.pagination > a {
    color: white;
    text-decoration: none;
    position: relative;
}

.pagination > a:after{
    position: absolute;
    content:"";
    width: 0;
    height: 2px;
    background: white;
    color: white;
    bottom: 0;
    right: 0;
    left: 0;
    transition: all .5s;   
}

.pagination > a:hover:after{
    width: 100%;
}

.footer{
    background-color: #424579;
    height: 70px;
    width: 100%;
    position: absolute;
    bottom: 0px;
}

.footer-wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    padding: 0px 15px;
}

.social-media{
    display: flex;
    gap:15px;
}

.layout_wrapper{
    padding-bottom: 170px;
}



/* MyMovies */ 

.myMovies_header{
    text-align: center;
    margin-bottom: 30px;
}

/* About */

.about{
    display: flex;
    flex-direction: column;
    font-size: 14px;
    gap: 35px;
    padding: 0px 25px 120px 25px;
}


/* Login */ 

.login_wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
}

.login{
    border: 1px solid #bac7e8;
    background-color: #bac7e8;
    border-radius: 10%;
    padding: 25px;
}

.login_header{
    text-align: center;
}

.login_form{
    display: flex;
    flex-direction: column;
    gap: 35px;
    margin: 35px;
    align-items: center;
}

.form_button{
    background-color: #6A8DE6;
    border: none;
    cursor: pointer;
    border-radius: 15px;
    padding: 10px;
    width: 60%;
    color: white;
    font-size: 1.2em;
}

.login_offer{
    text-align: center;
    font-size: 1.2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.login_offer a{
    color: #424579;
    cursor: pointer;
    font-size: 1.4rem;
    background-color: transparent;
    border: none;
    transition: all 0.3s;
}

.login_offer a:hover{
    scale: 1.3;
}

/* error */ 

.error{
    text-align: center;
}

.filter{
    margin-bottom: 30px;
    text-align: center;
}

.filter-content{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 500px;
    margin: auto;
    background-color: #6A8DE6;
    /* padding: 15px; */
    gap: 35px;
    border-radius: 15px;
    min-height: 0;
    transition: all 0.5s;
}

.filter-content.open{
    padding: 15px;
    margin: 10px auto 10px auto;
}

.collapse{
    display: grid;
    grid-template-rows: 0fr;
    overflow: hidden;
    transition: all 0.3s;
}

.collapse.active {
    grid-template-rows: 1fr;
}

.selects{
    display: flex;
    gap: 25px;
}


.select{
    background-color: transparent;
    color: white;
    border-radius: 5px;
    border: 1px solid #00081d;
    width: 100px;
}

.select>option{
    background-color: #00081d;
}

.sliders{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.slider{
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.rating-slider, .year-slider{
    width: 100%;
}


/* Slider */

.horizontal-slider{
    width: 100%;
    height: 50px;
}

.horizontal-slider .track{
    top: 14px;
    height: 10px;
    border-radius: 15px;
}

.horizontal-slider .track-0{
    background-color: #00081d;
}

.horizontal-slider .track-1{
    background-color: white;
    border: 1px solid #00081d;
}

.horizontal-slider .track-2{
    background-color: #00081d;
}

.horizontal-slider .thumb{
    color: #00081d;
    background-color: white;
    font-weight: bold;
    width: 35px;
    text-align: center;
    line-height: 32px;
    height: 35px;
    /* padding: 3px 7px; */
    border-radius: 100%;
    font-size: 0.8em;
    border: 2px solid #424579;
    cursor: pointer;
}

.keyword p{
    font-weight: bold;
    letter-spacing: 1px;
}

.keyword input{
    border-radius: 10px;
    border: 1px solid transparent;
    padding: 5px 20px;
    width: 250px;
    text-align: center;
    margin-top: 10px;
}

.filter_btn{
    background-color: #00081d;
    color: white;
    padding: 10px 30px;
    border-radius: 20px;
    border: 1px transparent;
    cursor: pointer;
    transition: all 0.3s;
}

.filter_btn:hover{
    scale: 1.3;
}

/* Similar movies */

.simMoviesHeader{
    text-align: center;
    margin-bottom: 50px;
}

.SimilarMovies{
    padding-bottom: 170px;
    display: grid;
    grid-template-rows: 300px;
    grid-template-columns: 350px 350px 350px;
    align-items: center;
    justify-content: center;
    gap: 70px;
    margin: auto;
}

.SimilarMovies > :last-child{
    grid-column: 2;
    justify-self: center;
 }

.similar-movie-object{
    font-size: 12px;
    color: white;
    display: flex;
    gap: 40px;
    background-color: #6A8DE6;
    border-radius: 25px;
    box-shadow: 10px 9px 20px 0px #91a1cc;
    padding: 30px 20px;
    transition: all 0.5s;
    width: 350px;
    cursor: pointer;
}

.similar-movie-object:hover{
    scale: 1.2;
}

.similar-movie-poster{
    width: 150px;
    height: 250px;
}

.similar-movie-description{
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.SimMoviesBtn{
    background-color: transparent;
    border: 1px solid #6A8DE6;
    padding: 10px 45px;
    border-radius: 50px;
    color: white;
    font-size: 20px;
    cursor: pointer;
    transition: all 0.3s;
}

.SimMoviesBtn:hover{
    background-color: #6A8DE6;
}

.isOpen {
    display: none;
}

/* Pagination */ 

.page_pagination{
    padding-bottom: 170px;
    display: flex;
    flex-wrap: wrap;
    margin: auto;
    justify-content: center;
    align-items: center;
    gap: 30px
}

.page{
    cursor: pointer;
    font-size: 20px;
    font-weight: bold;
    border: 1px solid #91a1cc;
    font-weight: bold;
    text-align: center;
    width: 30px;
    height: 30px;
    transition: all 0.3s;
}

.page:hover{
    background-color: #91a1cc;
}

.page_current{
    color: white;
    background: #91a1cc;
}

/* Message */ 

.message{
    font-size: 20px;
    font-weight: bold;
    text-align: center;
}

/* Media */ 

@media (max-width: 1090px){
    .movie-object:hover{
        scale: 1.1;
    }
}

@media (max-width: 900px){

}

@media (max-width: 760px){
    .header{
        margin-bottom: 50px;
    }
    .about_wrapper{
        max-width: 640px;
    }
    .logo{
        font-size: 25px;
    }
    .pagination{
        font-size: 15px;
        gap: 10px;
    }

    .movie-list{
        gap: 50px;
    }

    .search-querry{
        margin-bottom: 30px;
    }
    .search-querry-content{
        flex-direction: column;
    }

    .search-btn{
        width: 50%;
    }

    .movie-item__wrapper{
        max-width: 400px;
        margin: auto
    }
    .movie-item{
        width: fit-content;
    }

    .preview{
        flex-direction: column;
        width: fit-content;
        gap: 20px;
    }
    .description{
        margin: 0;
    }

    .trailer{
        display: none;
    }

    .genres-list{
        justify-content: center;
    }
    .btn{
        position: unset;
    }
}

@media (max-width: 500px){
    .about{
        font-size: 12px;
    }
    .movie-description{
        font-size: 16px;
        gap: 10px;
    }
    .pagination{
        font-size: 12px;
    }

    .rate{
        align-items: normal;
    }
    .rate-number{
        font-size: 1em;
    }

    .search-querry-content{
        font-size: 12px;
    }
    .search-btn{
        font-size: 18px;
        padding: 7px;
    }

    .movie-fact{
        flex-direction: column;
        gap: 7px;
    }
    .logo{
        font-size: 18px;
    }
    .movie-object{
        width: 355px;
        gap: 15px;
        padding: 15px 10px;
    }
    .movie-header{
        font-size: 1em;
    }
    .footer{
        font-size: 12px;
    }

    .filter-content{
        width: 300px;
    }

    .selects{
        flex-wrap: wrap;
        justify-content: center;
    }

}